<template>
  <view class="container">
    <view class="wechatapp">
      <view class="header">
        <open-data class="avatar" type="userAvatarUrl"></open-data>
      </view>
    </view>
    <view class="auth-title">申请获取以下权限</view>
    <view v-if="isProfile">
      <view class="auth-subtitle">获得您微信绑定的手机号码</view>
      <view class="login-btn">
        <button class="button-mobile btn-primary" open-type="getPhoneNumber"
          @getphonenumber="getPhoneNumber">授权手机号</button>
      </view>
    </view>
    <view v-if="!isProfile">
      <view class="auth-subtitle">获得你的公开信息（昵称、头像等）</view>
      <view class="login-btn">
        <button class="button btn-normal" @click.stop="getUserProfile">授权登录</button>
      </view>
    </view>
    <view class="no-login-btn">
      <button class="button btn-normal" @click="cancelLogin">暂不登录</button>
    </view>
    <view class="privacy">
      <label @click="privacyChange">
        <checkbox :checked="agreePrivacy" value="agree" color="#fa2209" style="transform:scale(0.7)" />已阅读并同意
      </label>
      <text class="privacy-ptl" @click="openPrivacy(1)">《隐私协议》</text>
      <text class="member-ptl" @click="openPrivacy(2)">《用户协议》</text>
      <wx-privacy ref="popPrivacy" @agree="handleAgree" :title="protocolTitle" :subDesc="protocolSubDesc"
        @disagree="handleDisagree"></wx-privacy>
    </view>

  </view>
</template>

<script>
import store from '@/store'
import * as UserApi from '@/api/user'
import WxPrivacy from './wx-privacy'
import ProtocolEnum from '@/common/enum/protocol/Protocol'
import SettingKeyEnum from '@/common/enum/setting/Key'
export default {
  components: {
    WxPrivacy
  },
  data() {
    return {
      // 微信小程序登录凭证 (code)
      // 提交到后端，用于换取openid
      code: '',
      needPhone: false,
      isProfile: false,
      isOpenPrivacy: false,
      agreePrivacy: false,
      protocolTitle: '',
      protocolSubDesc: '',
      ProtocolEnum,
      SettingKeyEnum
    }
  },

  created() {
    // 获取code
    this.getCode()

    // 获取配置
    this.getUserSetting()
  },

  methods: {
    privacyChange() {
      this.agreePrivacy = !this.agreePrivacy;
    },
    openPrivacy(type) {
      if (type == 1) {
        this.protocolTitle = ProtocolEnum.data[0].name;
        this.protocolSubDesc = ProtocolEnum.data[0].value;
      } else {
        this.protocolTitle = ProtocolEnum.data[1].name;
        this.protocolSubDesc = ProtocolEnum.data[1].value;
      }
      this.$refs.popPrivacy.openPrivacy();
    },
    handleDisagree() {
      this.agreePrivacy = false;
      this.$refs.popPrivacy.closePrivacy();
    },
    handleAgree() {
      this.agreePrivacy = true;
      this.$refs.popPrivacy.closePrivacy();
    },

    // 获取code
    // https://developers.weixin.qq.com/miniprogram/dev/api/open-api/login/wx.login.html
    getCode() {
      return new Promise((resolve, reject) => {
        uni.login({
          provider: 'weixin',
          success: res => {
            console.log('getCode res = ', res)
            resolve(res.code)
          },
          fail: reject
        })
      })
    },

    getUserSetting() {
      const app = this
      return new Promise((resolve, reject) => {
        UserApi.setting()
          .then(result => {
            app.needPhone = result.data.loginNeedPhone === 'true' ? true : false
          })
          .catch(err => {
            // empty
          })
      })
    },

    // 获取微信用户信息
    getUserProfile() {
      const app = this;
      if (!app.agreePrivacy) {
        app.$error("请仔细阅读《用户协议》和《隐私协议》，点击同意代表您已认上述协议内容！");
        return false;
      }
      wx.canIUse('getUserProfile') && wx.getUserProfile({
        lang: 'zh_CN',
        desc: '获取用户相关信息',
        success({ userInfo }) {
          console.log('userInfo == ', app)
          if (app.needPhone) {
            app.isProfile = true;
          }
          // 授权成功事件
          userInfo.type = "profile";
          app.onAuthSuccess(userInfo);
        },
        fail: function (res) {
          console.log('登录授权失败，请设置小程序隐私保护协议，返回信息： ', res);
          app.$error("抱歉，登录授权失败.");
        }
      })
    },

    // 获取微信绑定的手机号
    getPhoneNumber(e) {
      if (e.detail.errMsg == "getPhoneNumber:ok") {
        this.onAuthSuccess({ "type": "phone", "encryptedData": e.detail.encryptedData, "iv": e.detail.iv, "sessionKey": e.detail.iv })
      }
    },

    // 授权成功事件
    // 这里分为两个逻辑:
    // 1.将code和userInfo提交到后端，如果存在该用户 则实现自动登录，无需再填写手机号
    // 2.如果不存在该用户, 则显示注册页面, 需填写手机号
    async onAuthSuccess(userInfo) {
      const app = this
      // 提交到后端




      store.dispatch('MpWxLogin', { code: await app.getCode(), scene: "mnp", client: 1, mobile: "", password: "" })
        .then(result => {
          console.log("MpWxLogin_result", result);
          if (!app.needPhone) {
            // 显示登录成功
            app.$toast(result.message)
            // 跳转回原页面
            setTimeout(() => {
              app.onNavigateBack()
            }, 1000)
          } else {
            store.dispatch('Logout')
          }
        })
        .catch(() => {
          // 将oauth提交给父级
          app.onEmitSuccess(userInfo)
        })
    },

    // 将oauth提交给父级
    // 这里要重新获取code, 因为上一次获取的code不能复用(会报错)
    async onEmitSuccess(userInfo) {
      const app = this
      app.$emit('success', {
        oauth: 'MP-WEIXIN', // 第三方登录类型: MP-WEIXIN
        code: await app.getCode(), // 微信登录的code, 用于换取openid
        userInfo // 微信用户信息
      })
    },

    /**
     * 暂不登录
     */
    cancelLogin() {
      // 跳转回原页面
      this.onNavigateBack();
    },

    /**
     * 授权成功 跳转回原页面
     */
    onNavigateBack(delta = 1) {
      uni.navigateBack({
        delta: Number(delta)
      })
    }
  }
}
</script>

